.section_blog-cards {
    .section_blog-card {
        box-shadow: $box-shadow;
        margin-bottom: 15px;
    }
    .blog_card-description {
        padding: 20px;
    }
}

.blog_card-img {
    width: 100%;
    height: 250px;
}

.blog_card-img-1 {
    background: url(../assets/blog-promises.png) center / cover no-repeat $white;
}

.blog_card-img-2 {
    background: url(../assets/blog-svg.png) center / cover no-repeat $white;
}

.blog_card-img-3 {
    background: url(../assets/blog-juno.png) center / cover no-repeat $white;
}

@media (min-width:$breakpoint-lg) {
    .section_blog-cards {
        display: flex;
        justify-content: space-between;
        .section_blog-card {
            width: 32%;
            box-shadow: $box-shadow;
        }
        .blog_card-description {
            padding: 20px;
        }
    }
}
