.portfolio_item {
    padding: 25px 0;
    position: relative;
    .portfolio_item-img {
        background-color: $light-gray;
        padding: 10px;
    }
}

.portfolio_floral-1,
.portfolio_floral-2 {
    display: none;
}

.portfolio_item:nth-child(even) {
    flex-direction: row-reverse;
    .portfolio_item-img {
        position: relative;
        left: 9%;
        img {
            position: relative;
            right: 10%;
        }
    }
    
}

.portfolio_item:nth-child(odd) {
    .portfolio_item-img {
        position: relative;
        right: 9%;
        img {
            box-shadow: $box-shadow;
            position: relative;
            left: 10%;
        }
    }
    
}

.portfolio_item-description {
    margin: 10px;
    .project-tech {
        display: flex;
        flex-wrap: wrap;
        justify-content: start;
        li {
            font-weight: $font-weight-normal;
            background-color: $light-gray;
            padding: 5px 8px;
            margin-bottom: 8px;
            margin-right: 5px;
        }
    }

    .project-links {
        margin: 15px 0;
    }
    
    .project-links a:first-child {
        margin-right: 10px;
    }
    
}

@media (min-width:$breakpoint-lg) { 
    .portfolio_item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 50px 0;
        .portfolio_item-img {
            width: 53%;
        }

        .portfolio_item-description {
            width: 47%;
        }
        .portfolio_item-img {
            padding: 25px;
        }
    }

    .portfolio_floral-1,
    .portfolio_floral-2 {
        display: block;
        position: absolute;
        width: 400px;
        z-index: -10;
    }

    .portfolio_floral-1 {
        right: -30%;
        transform: rotate(-96deg);
        bottom: -19%;
    }

    .portfolio_floral-2 {
        bottom: -16%;
        left: -42%;
        transform: rotate(180deg);
    }
}

@media (min-width:$breakpoint-xl) { 
    .portfolio_floral-1 {
        z-index: 10;
    }
    .portfolio_floral-2 {
        left: -30%;
    }

    .portfolio_floral-1 {
        right: -22%;
    }
}